@import url("https://fonts.googleapis.com/css?family=Acme");

@font-face {
  font-family: 'norwesterregular';
  src: url('./fonts/norwester-webfont.eot');
  src: url('./fonts/norwester-webfont.eot?#iefix') format('embedded-opentype'),
       url('./fonts/norwester-webfont.woff2') format('woff2'),
       url('./fonts/norwester-webfont.woff') format('woff'),
       url('./fonts/norwester-webfont.ttf') format('truetype'),
       url('./fonts/norwester-webfont.svg#norwesterregular') format('svg');
  font-weight: normal;
  font-style: normal;
}

#root {
  height: 100%;
  position: relative;
}

.App {
  height: 100%;
  position: relative;
  text-align: center;
}

/* BASIC STYLES */

html {
  max-height: 100%;
  max-width: 100%;
  height: 100%;
}

body {
  height: 100%;
  position: relative;
}

/* WRAPPER */

.wrapper {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-height: 100%;
  padding: 0 0 100px;
  position: relative;
  background: url("./jungle.jpg") no-repeat center center fixed;
  background-size: cover;
}

@-webkit-keyframes fadeIn {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@-webkit-keyframes flipIt {
  from {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
  }
  to {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
  }
}

@keyframes flipIt {
  from {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
  }
  to {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
  }
}

@-webkit-keyframes flipBack {
  from {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
  }
  to {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
  }
}

@keyframes flipBack {
  from {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
  }
  to {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
  }
}



