.monkey {
    width: 20%;
    max-width: 250px;
    margin: 10px;
    border: 5px solid #F3EFF5;
    -webkit-box-shadow: 3px 3px 5px rgba(0,0,0,.6);
            box-shadow: 3px 3px 5px rgba(0,0,0,.6);
}

.monkey:hover {
    -webkit-transform: scale(1.01);
        -ms-transform: scale(1.01);
            transform: scale(1.01);
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    cursor: pointer;
}

.animate {
    -webkit-animation: fadeIn 1s ease-in-out, flipIt 2s ease-in-out;
            animation: fadeIn 1s ease-in-out, flipIt 2s ease-in-out;
    -webkit-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

#monkey01 {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}

#monkey02 {
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}

#monkey03 {
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}

#monkey04 {
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}

#monkey05 {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

#monkey06 {
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}

#monkey07 {
    -webkit-animation-delay: 1.4s;
            animation-delay: 1.4s;
}

#monkey08 {
    -webkit-animation-delay: 1.6s;
            animation-delay: 1.6s;
}

#monkey09 {
    -webkit-animation-delay: 1.8s;
            animation-delay: 1.8s;
}

#monkey10 {
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
}

#monkey11 {
    -webkit-animation-delay: 2.2s;
            animation-delay: 2.2s;
}

#monkey12 {
    -webkit-animation-delay: 2.4s;
            animation-delay: 2.4s;
}


/* Tablet portrait */

@media screen and (max-width: 991px) {
    .monkey {
        margin: 8px;
        border: 4px solid #F3EFF5;
        -webkit-box-shadow: 2px 2px 4px rgba(0,0,0,.6);
                box-shadow: 2px 2px 4px rgba(0,0,0,.6);
    }
}

/* Tablet portrait */

@media screen and (max-width: 798px) {
    .monkey {
        margin: 7px;
        border: 4px solid #F3EFF5;
        -webkit-box-shadow: 2px 2px 4px rgba(0,0,0,.6);
                box-shadow: 2px 2px 4px rgba(0,0,0,.6);
    }
}

@media screen and (max-width: 575px) {
    .monkey {
        width: 25%;
    }
}


/* Mobile phones */

@media screen and (max-width: 385px) {
    .monkey {
        margin: 5px;
        border: 2px solid #F3EFF5;
        -webkit-box-shadow: 1px 1px 3px rgba(0,0,0,.6);
                box-shadow: 1px 1px 3px rgba(0,0,0,.6);
    }
}


